  // State for managing stroke color update after animation trigger
  const [strokeFadeColor, setStrokeFadeColor] = useState(strokeColor);
  // State for triggering an animation class
  const [animate, setAnimate] = useState(false);
  // State for managing the hover effect on fill color
  const [isHovered, setIsHovered] = useState(false);

  // Define the default fill color and the hover fill color (both are strings)
  const defaultFillColor = initialFillColor || strokeColor; // fallback to black if not provided
  const hoverFillColor = strokeColor; // change to desired hover color

  // Determine which fill color to use based on hover state
  const currentFillColor = isHovered ? hoverFillColor : defaultFillColor;

  useEffect(() => {
    if (triggerAnimation) {
      // For example, update the stroke color after a delay
      setTimeout(() => {
        setStrokeFadeColor("#443d28"); // New stroke color after delay
      }, 2000); // Adjust the delay as needed

      // Optionally trigger a re-animation if needed
      setAnimate(false);
      setTimeout(() => setAnimate(true), 10);
    }
  }, [triggerAnimation]);

